<script>
import Navbar from "../components/Navbar";
import Clipboard from "clipboard";
import $ from "jquery";
import { mapGetters } from 'vuex'

export default {
   name: 'Layout',
  components: {
    Navbar,
  },
  computed:{
    ...mapGetters(['cart','local'])
  },
  data() {
    return {
      form: {
        user: {
          email: "",
        }
      }
    };
  },
  methods: {
    check() {
      this.$validator.validate().then(valid => {
        if (valid) {
          this.$store.dispatch('updateMessage', { message: '', status: 'success' })
        } else {
          this.$store.dispatch('updateMessage', { message: '', status: 'danger' })
        }
      });
      this.form.user.email = "";
    },
    copy() {
      var clipboard = new Clipboard(".tag-read");
      clipboard.on("success", e => {
        this.$store.dispatch('updateMessage', { message: '', status: 'success' })
        clipboard.destroy();
      });
      clipboard.on("error", e => {
        clipboard.destroy();
      });
    },
     setlocal(title , id){
        this.$store.dispatch('setlocal', { title, id});
    },
  },
  mounted() {
    $(window).scroll(() => {
      const scrollPos = $(window).scrollTop();
      const windowHeight = $(window).height();

        $('.move-right').each(function left() {
        const thisPos = $(this).offset().top;
        if (windowHeight + scrollPos >= thisPos + 200) {
          $(this).addClass('animated fadeInLeft');
        }
      });

      $('.move-left').each(function right() {
        const thisPos = $(this).offset().top;
        if (windowHeight + scrollPos >= thisPos + 200) {
          $(this).addClass('animated fadeInRight');
        }
      });
	  });
  }
};
</script>